<!-- -- -->
<template>
	<div class="bgcolor">
		<div class="title"></div>
		<qsxxb v-for="item,index in num" :key="item.sfzhm+index" :getSavedData="item" :value="djyz" @childData="getBack" @delChild="delChild"></qsxxb>
		<p class="add">
			<span @click="add"></span>
		</p>
		<div class="nextpage" @click="toCdxx">
			<mt-button size="large" type="primary"></mt-button>
		</div>
	<!-- 	<transition name="fade">
			<div class="success" v-show="isShow"></div>
		</transition> -->
	</div>
</template>

<script>
//
	import qsxxb from "@/pages/student/component/common/rxzb-wsxx-qsxx-info.vue"
	export default{
		name:'',
		components:{qsxxb},
		data(){
			return {
				//
				num:[{id:1}],
				//
				djyz:false,
				//
				isShow:false,
				//
				container:[],
				//
				save:[]
			}
		},
		methods:{
			toCdxx(){//
				this.djyz = !this.djyz
				//this.$router.push("/wsxxCdxx")
			},
			add(data){
				this.num.push({"cyxm":"","cygx":"","lxdh":"","cynl":"","gzhxxdw":"","nsr":"","gzhxxdwdz":"","jkzk":"","dwyb":"","sfzhm":"","zy":"","id":(new Date()).getTime()})



				// console.log(this.num);
			},
			//
			delChild(obj){
				this.num = this.num.filter(item => item!=obj)
				//let arr = this.num.filter(item => item==obj)
				// this.num = []
				// this.$nextTick(() => {
				// 	this.num = arr
				// })	
				// this.num = this.num.splice(num)
				// if(this.num.length == 1){
				// 	this.num = [];
				// }
			},
			//
			getBack(val){
				this.container.push(val.zt)
				this.save.push(val.value)
				console.log(this.container.length,this.num.length)
				if(this.container.length==this.num.length){//
					//true
					
					if([...new Set(this.container)][0]==true && [...new Set(this.container)].length == 1){//True
						//store
						var savewsxxPage2 = ""
						//
						for(var i = 0;i < this.save.length;i++){
							savewsxxPage2 += this.save[i]
						}
						this.$store.commit("savewsxxPage2",savewsxxPage2)
						this.$router.push("/wsxxCdxx")
						this.container = []
						this.save = []
					}else{
						this.isShow = !this.isShow
						setTimeout(()=>{
							this.isShow = !this.isShow
						},2000)
						//
						this.container = []
						this.save = []
					}

				}
			},
			
		},
		// updated(){
		// 	console.log("updated");
		// 	console.log(this.$store.state.getWsxxData);
		// },
		// beforeUpdate(){
		// 	console.log("beforeUpdate");

		// },
		created(){
			// console.log("created");
		},
		// beforeMount(){
		// 	console.log("beforeMount");
		// // },
		// mounted(){
		// 	console.log("mounted");
		// },
		activated(){
			//
			var data = this.$store.state.getWsxxData
			// console.log(data.qsxx)
			// console.log('test');
			if(data.qsxx.length==0){
				//
				// console.log(data.qsxx)
				// console.log("a");
				this.num = [{"cyxm":"","cygx":"","lxdh":"","cynl":"","gzhxxdw":"","nsr":"","gzhxxdwdz":"","jkzk":"","dwyb":"","sfzhm":"","zy":"","id":2}]
				// console.log(this.num)
			}else{
				console.log(data.qsxx);
				this.$nextTick(() => {
				this.num = data.qsxx
				})
				// console.log(JSON.parse(JSON.stringify(this.num)))
				// console.log("bb"); 
			}
		},
	}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
	.bgcolor
		position:fixed
		top:0
		bottom:0
		width:100%
		background:#f7f7f7
		overflow:auto
		.title
			font-size:0.13rem
			font-family:PingFang SC
			font-weight:bold
			line-height:0.31rem
			text-indent:0.12rem
			color:#333
		.nextpage
			padding:0 0.12rem 2rem
			margin-top:0.2rem
		.add
			display:flex
			height:0.51rem
			justify-content:center
			align-items:center
			background:#fff
			span
				padding-left:0.2rem	
				font-size:0.14rem
				font-family:PingFang-SC-Medium
				color:#408EDB	
				background:url(/yxxt/static/student-index-pictures/add.png) no-repeat
				background-size: .14rem;
				line-height:0.15rem
				height:0.15rem
		.success
			position:fixed
			margin:auto
			top:0
			left:0
			right:0
			bottom:0
			width:3rem
			height:.4rem
			border-radius:.06rem
			background:rgba(0, 0, 0, .7)
			line-height:.4rem
			text-align:center
			font-size:.14rem
			color:#fff
		.fade-enter-active,.fade-leave-active
			transition:opacity .5s
		.fade-enter,.fade-leave-to
			opacity:0
</style>


